<template>
    <div class="clients__wrapper">
        <MenuHeader>
            <template v-slot:title>
                <p class="client__title"></p>
            </template>
        </MenuHeader>
        <p class="client__amount">
            : {{clients.length}}
        </p>
        <div class="clientCards">
            <Client v-for="client in clients" :key="client.id" :client="client"/>
        </div>
        <bottom-menu active="clients"/>
    </div>
</template>
<script>
import Client from '../components/Client.vue'
import BottomMenu from '../components/BottomMenu.vue'
import MenuHeader from '../components/MenuHeader.vue'
import axios from 'axios'
import {mapState} from 'vuex'
export default {
    components: {Client, BottomMenu,MenuHeader},
    data(){
        return{
            clients: []
        }
    },
    mounted(){
        axios.get(`https://almvtst.ml/crm/user/${window.$cookies.get('id')}/facility/${this.facility.id}/users`, {
            headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}
        }).then(e=>this.clients = e.data.clients);
    },
    computed: {
        ...mapState('facility', ['facility'])
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.clients__wrapper{
    min-height: 100vh;
    background: #E5E5E5;
    .client__amount{
        padding: 16px;
    }
    .client__title{
        font-weight: 500;
        font-size: 20px;
        line-height: 32px;
    }
    .clientCards{
        margin-bottom: 49px;
    }
}
</style>