<template>
<div class="timetable__wrapper" ref="main">
    <MainHeader filterType = "allTime" />
    <div v-for="day, index in dates" :key="index" class="content">
        <DateHeader :date="day" :buttonNeeded="false"/>
        <Timetable :id="index" :date="day" :infoAboutDay="setInfo(day.day())" @move="move" />
    </div>
    <BottomMenu active= "calendar" />
</div>
</template>
<script>
import Timetable from '../components/Timetable.vue'
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
import MainHeader from '../components/MainHeader.vue'
import DateHeader from '../components/DateHeader.vue'
import BottomMenu from '../components/BottomMenu.vue'
import {mapState} from 'vuex'
export default {
    components: {
        Timetable,
        MainHeader,
        DateHeader,
        BottomMenu
    },
    data(){
        return{
            dates: [], 
            today: dayjs().locale('ru'),
        }
    },
    async created(){
        this.setDate()
    },
    mounted(){
        this.scrollToToday();
    },
    computed:{
        ...mapState('admin', ['user']),
        ...mapState('facility', ['facility'])
    },
    methods:{
        setDate(){
            let day = dayjs().locale('ru')
            day = day.subtract(7, 'day').hour(0).minute(0).second(0)
            for(let i = 0;i<30;i++){
                this.dates.push(day)
                day = day.add(1, 'day')
            }
        },
        /* setInfo(dayOfWeek){
            return this.getFacility(dayOfWeek).map(e=>{
                if(!e.periods_data[dayOfWeek].dayoff){
                    return e.periods_data[dayOfWeek]
                }
            });
        }, */
        setInfo(dayOfWeek){
            let infoFieldsDaily = {}
            this.getFacility(dayOfWeek).forEach(e=>{
                infoFieldsDaily = {...infoFieldsDaily, [e.id]:e.periods_data[dayOfWeek]}
            })
            return infoFieldsDaily
        }, 
        getFacility(day){
            return this.facility.playfields?.filter(e=>!e.periods_data[parseInt(day)].dayoff)??[]
        },
        scrollToToday(){
            const TIMETABLE__HEIGHT = 114
            const TIMETABLE__BEFORE = 7
            window.scrollTo(0, TIMETABLE__HEIGHT*TIMETABLE__BEFORE)
            window.onbeforeunload = function () {
                window.scrollTo(0, TIMETABLE__HEIGHT*TIMETABLE__BEFORE)
            }
        },
        move(l){
            this.$refs['main'].scrollLeft+=l*4/5;
        }
    }

}
</script>
<style lang = "scss" scoped>
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.content{
    width: max-content;
    min-width: 2487px;
}

.timetable__wrapper{
    overflow-x: scroll;
    padding-bottom: 49px;
    &:first-child{
        margin-top: 93px;
    }
}

</style>